"use client"

import type React from "react"

import { useEffect, useState } from "react"
import { ChevronRight, Check } from "lucide-react"

const steps = [
  { number: 1, title: "Tell Us About You", description: "Share your project goals and requirements" },
  { number: 2, title: "Consultation Call", description: "Free 30-minute strategy session" },
  { number: 3, title: "Proposal & Timeline", description: "Detailed plan and project roadmap" },
  { number: 4, title: "Kick Off", description: "Start building your solution" },
]

export default function GetStarted() {
  const [isLoaded, setIsLoaded] = useState(false)
  const [activeStep, setActiveStep] = useState(0)
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    number : "",
    company: "",
    service: "web-development",
    budget: "10k-25k",
    brief: "",
  })

  useEffect(() => {
    setIsLoaded(true)
  }, [])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    console.log("Form submitted:", formData)
    setActiveStep(1)
  }

  return (
    <main >
      {/* Hero Section */}
      <section className="relative py-24 bg-background overflow-hidden">
        <div className="absolute inset-0 opacity-20">
          <div className="absolute top-0 left-1/4 w-96 h-96 bg-[#A3C6C4] rounded-full mix-blend-multiply filter blur-3xl animate-morph"></div>
        </div>

        <div className="relative z-10 container mx-auto px-4 sm:px-6 lg:px-8">
          <div
            className={`max-w-4xl mx-auto transition-all duration-1000 ${isLoaded ? "opacity-100 translate-y-0" : "opacity-0 translate-y-10"}`}
          >
            <div className="inline-block mb-4">
              <span className="pill-badge bg-[#A3C6C4]/10 text-[#354649] border border-[#A3C6C4]/30">Get Started</span>
            </div>

            <h1 className="text-5xl md:text-6xl font-bold text-[#354649] mb-6">Start Your Project Today</h1>

            <p className="text-xl text-[#6C7A89]">
              Complete this form and we'll reach out within 24 hours to discuss your project and provide a tailored
              proposal.
            </p>
          </div>
        </div>
      </section>

      {/* Process Steps */}
      <section className="relative py-24 bg-background">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8 mb-24">
          <h2 className="text-3xl font-bold text-[#354649] mb-12">How It Works</h2>

          <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
            {steps.map((step, idx) => (
              <div key={idx} className="relative">
                {idx < steps.length - 1 && (
                  <div className="hidden md:block absolute top-8 left-[60%] w-[80%] h-1 bg-gradient-to-r from-[#A3C6C4]/40 to-transparent"></div>
                )}

                <div
                  className={`p-6 rounded-2xl bg-gradient-to-br from-[#A3C6C4]/5 to-[#6C7A89]/5 border border-[#A3C6C4]/20 relative z-10 transition-all duration-300 ${
                    activeStep >= idx ? "border-[#A3C6C4]/60 bg-[#A3C6C4]/10" : ""
                  }`}
                >
                  <div className="flex items-center gap-3 mb-4">
                    <div
                      className={`w-8 h-8 rounded-full flex items-center justify-center font-bold text-sm ${
                        activeStep >= idx ? "bg-[#A3C6C4] text-white" : "bg-[#A3C6C4]/20 text-[#354649]"
                      }`}
                    >
                      {activeStep > idx ? <Check size={16} /> : step.number}
                    </div>
                  </div>
                  <h3 className="font-bold text-[#354649] mb-2">{step.title}</h3>
                  <p className="text-sm text-[#6C7A89]">{step.description}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Form Section */}
      <section className="relative py-24 bg-background">
        <div className="container mx-auto px-4 sm:px-6 lg:px-8">
          <div className="max-w-3xl mx-auto">
            <form onSubmit={handleSubmit} className="space-y-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                {/* Name */}
                <div>
                  <label className="block text-sm font-semibold text-[#354649] mb-2">Full Name *</label>
                  <input
                    type="text"
                    name="name"
                    value={formData.name}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                    placeholder="John Doe"
                  />
                </div>

                {/* Email */}
                <div>
                  <label className="block text-sm font-semibold text-[#354649] mb-2">Email *</label>
                  <input
                    type="email"
                    name="email"
                    value={formData.email}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                    placeholder="john@example.com"
                  />
                </div>

                {/* Company */}
                <div>
                  <label className="block text-sm font-semibold text-[#354649] mb-2">Company</label>
                  <input
                    type="text"
                    name="company"
                    value={formData.company}
                    onChange={handleChange}
                    className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                    placeholder="Your Company"
                  />
                </div>

                {/* Service */}
                <div>
                  <label className="block text-sm font-semibold text-[#354649] mb-2">Service *</label>
                  <select
                    name="service"
                    value={formData.service}
                    onChange={handleChange}
                    className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                  >
                    <option value="web-development">Web Development</option>
                    <option value="digital-marketing">Digital Marketing</option>
                    <option value="video-editing">Video Editing</option>
                  </select>
                </div>

                {/* Budget */}
                <div>
                  <label className="block text-sm font-semibold text-[#354649] mb-2">Budget Range *</label>
                  <select
                    name="budget"
                    value={formData.budget}
                    onChange={handleChange}
                    className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                  >
                    <option value="10k-25k">$10K - $25K</option>
                    <option value="25k-50k">$25K - $50K</option>
                    <option value="50k-100k">$50K - $100K</option>
                    <option value="100k+">$100K+</option>
                  </select>
                </div>
              </div>

              {/* Brief */}
              <div>
                <label className="block text-sm font-semibold text-[#354649] mb-2">Project Brief *</label>
                <textarea
                  name="brief"
                  value={formData.brief}
                  onChange={handleChange}
                  required
                  rows={6}
                  className="w-full px-4 py-3 rounded-lg border border-[#A3C6C4]/30 bg-white focus:border-[#A3C6C4] focus:outline-none transition-colors"
                  placeholder="Tell us about your project, goals, and timeline..."
                />
              </div>

              {/* Submit Button */}
              <button
                type="submit"
                className="w-full px-8 py-4 rounded-full bg-gradient-to-r from-[#A3C6C4] to-[#6C7A89] text-white font-semibold hover:shadow-2xl transition-all duration-300 flex items-center justify-center gap-2 group"
              >
                Send Proposal Request
                <ChevronRight size={20} className="group-hover:translate-x-1 transition-transform" />
              </button>
            </form>
          </div>
        </div>
      </section>
    </main>
  )
}
